"use client";

import { useState } from "react";
import Button from "./components/Button";

export default function Home() {
  const [isScrollHovered, setIsScrollHovered] = useState(false);

  const scrollToSection = () => {
    // Scroll naar de tweede sectie
    document.querySelector("section:nth-of-type(2)").scrollIntoView({
      behavior: "smooth",
    });
  };

  return (
    <div className="relative w-full overflow-x-hidden">
      {/* Hero Section */}
      <section className="relative w-[100vw] h-[100vh] overflow-hidden bg-black">
        {/* Background video */}
        <video
          className="fixed inset-0 w-full h-full object-cover"
          style={{ filter: "brightness(0.6)" }}
          src="/background.mp4"
          autoPlay
          loop
          muted
          playsInline
        />

        {/* Content container */}
        <div className="absolute inset-0 flex flex-col items-center justify-center px-[5vw] text-center">
          <p className="text-[1.5vw] font-bold tracking-widest mb-[1.5vw] text-[var(--light-gray)] pt-[8vw] pb-[1.5vw] opacity-80">
            COLLABORATIVE STRENGTH.
          </p>

          {/* Hoofdtitel - zonder witte achtergrond */}
          <h1 className="text-[5.5vw] font-black leading-[5vw] mb-[3vw] max-w-[80vw] text-[var(--light-gray)] relative">
            <span className="text-[var(--light-gray)] mix-blend-difference inline-block pb-[4vw]">
              POWERLIFTING COACHING <br /> OP INDIVIDUELE BASIS
            </span>
          </h1>

          <div>
            <Button href="/contact" text="BOEK EEN GESPREK" />
          </div>
        </div>

        {/* Scroll indicator - ge√Øntegreerd in Home component */}
        <button
          onClick={scrollToSection}
          onMouseEnter={() => setIsScrollHovered(true)}
          onMouseLeave={() => setIsScrollHovered(false)}
          className="absolute bottom-[5vw] right-[5vw] z-30 flex items-center text-[var(--light-gray)] text-[1.5vw] cursor-pointer group"
        >
          <span
            className={`mr-[1vw] transition-colors duration-300 ${
              isScrollHovered
                ? "text-[var(--teal)]"
                : "text-[var(--light-gray)]"
            }`}
          >
            Scroll down
          </span>
          <div
            className={`relative w-[3vw] h-[3vw] rounded-full flex items-center justify-center transition-colors duration-300 ${
              isScrollHovered ? "bg-[var(--teal)]" : "bg-transparent"
            }`}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 -960 960 960"
              className={`w-[1.5vw] h-[1.5vw] fill-current transition-transform duration-500 ${
                isScrollHovered
                  ? "fill-[var(--dark)] rotate-[360deg]"
                  : "fill-[var(--light-gray)] rotate-[45deg]"
              }`}
            >
              <path d="M584-412H114v-136h470L382-750l98-96 366 366-366 366-97-96 201-202Z" />
            </svg>
          </div>
        </button>
      </section>

      {/* Section 2 */}
      <section className="relative w-[100vw] min-h-[100vh] bg-[var(--light-gray)] text-black px-[5vw] py-[10vh]">
        <div className="text-[3vw]">hello world</div>
      </section>
    </div>
  );
}
